import { useState } from "react";
import { useDispatch } from "react-redux";
import { Button } from "../../Components/Button/Button";
import { ItemList } from "../../Components/ItemList/ItemList";
import { Todo } from "../../reducers/newSlice";
import "./TodoList.css"
import { generateGuid } from "../../Utils/GenerateGuid";
import { Modal } from "../../Components/Modal/Modal";

export interface ITodoListProps {
    items: Todo[];
    title: string;
    addFunction: (item: Todo) => any;
    deleteFunction: (item: Todo) => any;
    changeDoneFunction?: (item: Todo) => any;
    deleteTodosFunction?: (items: Todo[]) => any;
    setTodosFunction?: (items: Todo[]) => any;
}

export const TodoList = (props: ITodoListProps) => {
    const { items, title, addFunction, deleteFunction, changeDoneFunction, deleteTodosFunction, setTodosFunction } = props; 
    const [open, setOpen] = useState<boolean>(false);
    const [inputValue, setInputValue] = useState<string>("");
    const dispatch = useDispatch()
    const [currentTask, setCurrentTask] = useState<Todo | null>(null);

    const onDragStartHandler = (e: React.DragEvent, task: Todo) => {
        setCurrentTask(task);
    }

    const onDropHandler = (e: React.DragEvent, task: Todo) => {
        if(!setTodosFunction) return;
        const todos = items.map(el => {
            if(el.id === task.id && currentTask!== null) {
                return {...el, order: currentTask.order}
            }
            if(el.id === currentTask?.id && currentTask!== null) {
                return {...el, order: task.order}
            }
            return el;
        })
        dispatch(setTodosFunction(todos.sort(sortCards)))
        
    }

    const sortCards = (a: Todo,b:Todo) => {
        if (a.order > b.order) {
            return 1
        } else {
            return -1
        }
   } 

    const addItem = () => {
        if (inputValue) {
            const task = {
                id: generateGuid(),
                title: inputValue,
                done: false,
                order: items.length
            }
            dispatch(addFunction(task));
        }
        setInputValue("");
        setOpen(false);
    }

    const deleteItem = (task: Todo) => {
        dispatch(deleteFunction(task))
    }

    const deleteItems = () => {
        if (!deleteTodosFunction) return;
        const deletedItems = items.filter(item => item.done);
        if (deletedItems && deletedItems.length) {
            dispatch(deleteTodosFunction(deletedItems))
        }
    }

    const changeDone = (task: Todo) => {
        if (!changeDoneFunction) return;
        dispatch(changeDoneFunction(task))
    }

    const onCancel = () => {
        setInputValue("");
        setOpen(false);
    }

    const buttons = [
        <Button text={"Отменить"} onClick={onCancel}></Button>,
        <Button text={"Создать"} onClick={addItem}></Button>
      ]

    return (
        <>
            <div className="todo-list">
                <div className={"todo-list-header"}>
                    <h1 className={"todo-list-header-text"}>{title}</h1>
                    <div className={"todo-list__buttons-block"}>
                        <Button text={"Добавить задачу"} iconClassName={"add-button-icon"} onClick={() => setOpen(true)}></Button>
                        <Button text={"Удалить выполненные"} iconClassName={"delete-button-icon"} onClick={deleteItems}></Button>
                    </div>
                    
                </div>
                {items.map((item) => 
                    <ItemList changeDone={changeDone} deleteItem={deleteItem} key={item.id} task={item} onDragStartHandler={onDragStartHandler} onDropHandler={onDropHandler}/>
                )}
            </div>
            <Modal open={open} setOpen={setOpen} buttons={buttons} onCancel={onCancel}>
                <div className={"modal-content"}>
                    <div>{"Введите название задачи"}</div>
                    <input value={inputValue} onChange={(e)=>setInputValue(e.target.value)}></input>
                </div>
            </Modal>
        </>
        
    )
}