import { useState } from "react";
import { Todo } from "../../reducers/newSlice";
import "./ItemList.css"

export interface IItemListProps {
    task: Todo;
    deleteItem: (task: Todo) => void;
    changeDone: (task:Todo) => void;
    onDragStartHandler: (e: any, task:Todo) => void;
    onDropHandler: (e: any, task:Todo) => void;
}

export const ItemList = (props: IItemListProps) => {
    const { task, changeDone } = props;

    const onDragOverHandler = (e: React.DragEvent) => {
        e.preventDefault();
    }
    return <div onClick={() => changeDone(task)} className={task.done ? "item-list done" : "item-list"}  draggable={true} 
    onDragStart={(e) => props.onDragStartHandler(e, task)} onDrop={(e) => props.onDropHandler(e, task)} onDragOver={onDragOverHandler }>
            <div className="item-list_name">{task.title}</div>
            <button onClick={() => props.deleteItem(task)} className="item-list_delete-button"></button>
        </div>
}