import FullCalendar from '@fullcalendar/react'
import dayGridPlugin from '@fullcalendar/daygrid'
import { useDispatch, useSelector } from 'react-redux';
import { RootState } from '../../reducers';
import interactionPlugin, { Draggable } from '@fullcalendar/interaction';
import { addEvent, deleteEvent, IEventItem } from '../../reducers/newSlice';
import { useState } from 'react';
import { Modal } from '../../Components/Modal/Modal';
import { Button } from '../../Components/Button/Button';


export function WeekDemoApp() {
    const events = useSelector((state: RootState) => state.todos.events);
    const [open, setOpen] = useState<boolean>(false);
    const [inputValue, setInputValue] = useState<string>("");
    const [currentDate, setCurrentDate] = useState(null);
    const [currentDateEl, setCurrentDateEl] = useState(null);
    const dispatch = useDispatch()

    

    const eventChanged = (eventDropInfo:any) => {
        const oldEvent = {title: eventDropInfo.oldEvent.title, start:eventDropInfo.oldEvent.start}
        dispatch(deleteEvent(oldEvent))
        const currentEvent = {title: eventDropInfo.event.title, start:eventDropInfo.event.start}
        dispatch(addEvent(currentEvent))
    }

    const addEventHandler = (dateClickInfo: any) => {
      (dateClickInfo.dayEl as HTMLElement).classList.add("selected");
      setCurrentDateEl(dateClickInfo.dayEl)
      console.log(dateClickInfo)
      setCurrentDate(dateClickInfo.date)
      setOpen(true);
    }

    const addItem = () => {
      if (currentDate) {
        const currentEvent = {title: inputValue, start: currentDate}
        dispatch(addEvent(currentEvent))
      }
      currentDateEl && (currentDateEl as HTMLElement).classList.remove("selected")
      setOpen(false);
      setInputValue("");
      
    }

    const onCancel = () => {
      setOpen(false)
      currentDateEl && (currentDateEl as HTMLElement).classList.remove("selected")
    }

  const buttons = [
    <Button text={"Отменить"} onClick={onCancel}></Button>,
    <Button text={"Создать"} onClick={addItem}></Button>
  ]


  return (
    <div>
      <FullCalendar
        locale="ru" 
        plugins={[interactionPlugin, dayGridPlugin]}
        initialView='dayGridWeek'
        weekends={true}
        events={events}
        eventContent={renderEventContent}
        editable={true}
        droppable={true}
        eventDrop={eventChanged}
        firstDay={1}
        dateClick={addEventHandler}
      />
      <Modal open={open} setOpen={setOpen} buttons={buttons} onCancel={onCancel}>
          <div className={"modal-content"}>
              <div>{"Введите название задачи"}</div>
              <input value={inputValue} onChange={(e)=>setInputValue(e.target.value)}></input>            
          </div>
      </Modal>
    </div>
  )
}

// a custom render function
function renderEventContent(eventInfo: any) {
  return (
    <>
      <i>{eventInfo.event.title}</i>
    </>
  )
}